.chatWindow {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--bg-primary);
}

.chatHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--bg-secondary);
}

.chatInfo {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
  min-width: 0;
}

.chatAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  background-color: var(--accent-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.chatAvatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatarPlaceholder {
  color: white;
  font-weight: bold;
  font-size: 16px;
}

.chatDetails {
  flex: 1;
  min-width: 0;
}

.chatName {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
  color: var(--text-primary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.chatStatus {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.chatActions {
  display: flex;
  gap: 8px;
}

.actionButton {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: var(--radius-medium);
  background-color: transparent;
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  transition: background-color 0.2s ease;
}

.actionButton:hover {
  background-color: var(--bg-hover);
}

.messagesContainer {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.messagesList {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.loadingContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
  gap: 16px;
  color: var(--text-secondary);
}

.emptyState {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: var(--text-secondary);
  font-size: 18px;
}

.typingIndicator {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background-color: var(--bg-secondary);
  border-radius: var(--radius-large);
  margin-left: 60px;
  max-width: 200px;
}

.typingDots {
  display: flex;
  gap: 4px;
}

.typingDots span {
  width: 8px;
  height: 8px;
  background-color: var(--text-secondary);
  border-radius: 50%;
  animation: typing 1.4s infinite ease-in-out;
}

.typingDots span:nth-child(1) {
  animation-delay: -0.32s;
}

.typingDots span:nth-child(2) {
  animation-delay: -0.16s;
}

@keyframes typing {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

.typingIndicator p {
  font-size: 14px;
  color: var(--text-secondary);
  margin: 0;
}

.messageInputContainer {
  padding: 20px;
  border-top: 1px solid var(--border-color);
  background-color: var(--bg-secondary);
}

.messageForm {
  width: 100%;
}

.inputWrapper {
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-large);
  padding: 8px 16px;
  transition: border-color 0.2s ease;
}

.inputWrapper:focus-within {
  border-color: var(--accent-primary);
}

.messageInput {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  color: var(--text-primary);
  font-size: 16px;
  padding: 8px 0;
}

.messageInput::placeholder {
  color: var(--text-muted);
}

.sendButton {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 50%;
  background-color: var(--accent-primary);
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  transition: background-color 0.2s ease;
  flex-shrink: 0;
}

.sendButton:hover:not(:disabled) {
  background-color: #0056b3;
}

.sendButton:disabled {
  background-color: var(--text-muted);
  cursor: not-allowed;
}

/* Responsive design */
@media (max-width: 768px) {
  .chatHeader {
    padding: 12px 16px;
  }

  .chatAvatar {
    width: 36px;
    height: 36px;
  }

  .avatarPlaceholder {
    font-size: 14px;
  }

  .chatName {
    font-size: 16px;
  }

  .messagesList {
    padding: 16px;
    gap: 12px;
  }

  .messageInputContainer {
    padding: 16px;
  }

  .inputWrapper {
    padding: 6px 12px;
  }

  .messageInput {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .chatHeader {
    padding: 10px 12px;
  }

  .messagesList {
    padding: 12px;
  }

  .messageInputContainer {
    padding: 12px;
  }
}

