.chatContainer {
  display: flex;
  height: 100vh;
  background-color: var(--bg-primary);
  color: var(--text-primary);
}

.sidebar {
  width: 350px;
  background-color: var(--bg-secondary);
  border-right: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.sidebarHeader {
  padding: 20px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--bg-primary);
}

.userInfo {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  background-color: var(--accent-primary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatarPlaceholder {
  color: white;
  font-weight: bold;
  font-size: 18px;
}

.userDetails {
  flex: 1;
  min-width: 0;
}

.userName {
  font-size: 16px;
  font-weight: 600;
  margin: 0;
  color: var(--text-primary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.userStatus {
  font-size: 12px;
  color: var(--text-secondary);
  margin: 0;
}

.headerActions {
  display: flex;
  gap: 8px;
}

.actionButton {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: var(--radius-medium);
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  transition: background-color 0.2s ease;
}

.actionButton:hover {
  background-color: var(--bg-hover);
}

.newChatSection {
  padding: 20px;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--bg-primary);
}

.searchInput {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-medium);
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  font-size: 14px;
  outline: none;
  transition: border-color 0.2s ease;
}

.searchInput:focus {
  border-color: var(--accent-primary);
}

.searchInput::placeholder {
  color: var(--text-muted);
}

.userList {
  margin-top: 12px;
  max-height: 200px;
  overflow-y: auto;
}

.userItem {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  border-radius: var(--radius-medium);
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.userItem:hover {
  background-color: var(--bg-hover);
}

.userAvatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  overflow: hidden;
  background-color: var(--accent-primary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.userAvatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.userAvatar .avatarPlaceholder {
  color: white;
  font-weight: bold;
  font-size: 14px;
}

.userInfo {
  flex: 1;
  min-width: 0;
}

.userInfo .userName {
  font-size: 14px;
  font-weight: 500;
  margin: 0;
  color: var(--text-primary);
}

.userUsername {
  font-size: 12px;
  color: var(--text-secondary);
  margin: 0;
}

.chatListContainer {
  flex: 1;
  overflow: hidden;
}

.mainContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: var(--bg-primary);
}

.welcomeScreen {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-primary);
}

.welcomeContent {
  text-align: center;
  padding: 40px;
}

.welcomeContent h2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 16px;
}

.welcomeContent p {
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin-bottom: 32px;
}

/* Responsive design */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    position: absolute;
    z-index: 10;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .mainContent {
    width: 100%;
  }

  .sidebarHeader {
    padding: 16px;
  }

  .newChatSection {
    padding: 16px;
  }
}

@media (max-width: 480px) {
  .sidebarHeader {
    padding: 12px;
  }

  .newChatSection {
    padding: 12px;
  }

  .welcomeContent {
    padding: 20px;
  }

  .welcomeContent h2 {
    font-size: 1.5rem;
  }

  .welcomeContent p {
    font-size: 1rem;
  }
}

