.chatList {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
  background-color: var(--bg-secondary);
  border-right: 1px solid var(--border-color);
}

.loadingContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: var(--text-secondary);
}

.loadingContainer .loading-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid var(--border-color);
  border-top: 2px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.emptyContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  text-align: center;
  color: var(--text-secondary);
}

.emptySubtext {
  font-size: 0.875rem;
  margin-top: 0.5rem;
  opacity: 0.7;
}

.chatItem {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  cursor: pointer;
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.2s ease;
  position: relative;
}

.chatItem:hover {
  background-color: var(--bg-hover);
}

.chatItem.selected {
  background-color: var(--primary-color-light);
  border-left: 3px solid var(--primary-color);
}

.chatAvatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 0.75rem;
  flex-shrink: 0;
}

.chatAvatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.avatarPlaceholder {
  width: 100%;
  height: 100%;
  background-color: var(--primary-color);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 1.125rem;
}

.chatContent {
  flex: 1;
  min-width: 0;
}

.chatHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.25rem;
}

.chatName {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.chatTime {
  font-size: 0.75rem;
  color: var(--text-secondary);
  flex-shrink: 0;
  margin-left: 0.5rem;
}

.chatPreview {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.lastMessage {
  font-size: 0.8125rem;
  color: var(--text-secondary);
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex: 1;
}

.unreadBadge {
  background-color: var(--primary-color);
  color: white;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.125rem 0.375rem;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
  margin-left: 0.5rem;
  flex-shrink: 0;
}

.groupIndicator {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  font-size: 0.75rem;
  opacity: 0.7;
}

/* Dark theme adjustments */
@media (prefers-color-scheme: dark) {
  .chatList {
    background-color: var(--bg-secondary-dark, #1a1a1a);
    border-right-color: var(--border-color-dark, #333);
  }
  
  .chatItem {
    border-bottom-color: var(--border-color-dark, #333);
  }
  
  .chatItem:hover {
    background-color: var(--bg-hover-dark, #2a2a2a);
  }
  
  .chatItem.selected {
    background-color: var(--primary-color-light-dark, rgba(59, 130, 246, 0.1));
  }
  
  .chatName {
    color: var(--text-primary-dark, #ffffff);
  }
  
  .chatTime,
  .lastMessage {
    color: var(--text-secondary-dark, #a0a0a0);
  }
  
  .loadingContainer,
  .emptyContainer {
    color: var(--text-secondary-dark, #a0a0a0);
  }
}

/* Responsive design */
@media (max-width: 768px) {
  .chatItem {
    padding: 0.5rem 0.75rem;
  }
  
  .chatAvatar {
    width: 40px;
    height: 40px;
    margin-right: 0.5rem;
  }
  
  .chatName {
    font-size: 0.8125rem;
  }
  
  .lastMessage {
    font-size: 0.75rem;
  }
}

