/* ChatSearch.module.css */

.searchOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 20px;
}

.searchContainer {
  background: white;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow: hidden;
  animation: slideDown 0.2s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.searchHeader {
  display: flex;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid #e5e7eb;
  gap: 12px;
}

.searchInputContainer {
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
}

.searchIcon {
  position: absolute;
  left: 12px;
  color: #6b7280;
  z-index: 1;
}

.searchInput {
  width: 100%;
  padding: 12px 12px 12px 40px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.2s;
}

.searchInput:focus {
  border-color: #3b82f6;
}

.clearButton {
  position: absolute;
  right: 8px;
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.clearButton:hover {
  background-color: #f3f4f6;
}

.closeButton {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: background-color 0.2s;
}

.closeButton:hover {
  background-color: #f3f4f6;
}

.searchResults {
  max-height: 400px;
  overflow-y: auto;
}

.resultsHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}

.resultsCount {
  font-size: 14px;
  color: #6b7280;
  font-weight: 500;
}

.navigationButtons {
  display: flex;
  align-items: center;
  gap: 8px;
}

.navButton {
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  padding: 6px;
  cursor: pointer;
  color: #374151;
  transition: all 0.2s;
}

.navButton:hover:not(:disabled) {
  background-color: #f3f4f6;
  border-color: #9ca3af;
}

.navButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.navCounter {
  font-size: 12px;
  color: #6b7280;
  font-weight: 500;
  min-width: 40px;
  text-align: center;
}

.resultsList {
  max-height: 300px;
  overflow-y: auto;
}

.resultItem {
  padding: 12px 16px;
  border-bottom: 1px solid #f3f4f6;
  cursor: pointer;
  transition: background-color 0.2s;
}

.resultItem:hover {
  background-color: #f9fafb;
}

.resultItem.active {
  background-color: #eff6ff;
  border-left: 3px solid #3b82f6;
}

.resultContent {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
}

.resultText {
  flex: 1;
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
  color: #374151;
  word-break: break-word;
}

.resultTime {
  font-size: 12px;
  color: #6b7280;
  white-space: nowrap;
  margin-top: 2px;
}

.noResults {
  padding: 40px 16px;
  text-align: center;
  color: #6b7280;
}

.noResults p {
  margin: 0;
  font-size: 14px;
}

/* Подсветка найденного сообщения */
.highlighted {
  background-color: #fef3c7 !important;
  animation: highlight 2s ease-out;
}

@keyframes highlight {
  0% {
    background-color: #fef3c7;
  }
  100% {
    background-color: transparent;
  }
}

/* Темная тема */
@media (prefers-color-scheme: dark) {
  .searchContainer {
    background: #1f2937;
    color: white;
  }
  
  .searchHeader {
    border-bottom-color: #374151;
  }
  
  .searchInput {
    background: #374151;
    border-color: #4b5563;
    color: white;
  }
  
  .searchInput:focus {
    border-color: #60a5fa;
  }
  
  .searchIcon,
  .clearButton,
  .closeButton {
    color: #9ca3af;
  }
  
  .clearButton:hover,
  .closeButton:hover {
    background-color: #374151;
  }
  
  .resultsHeader {
    background-color: #111827;
    border-bottom-color: #374151;
  }
  
  .resultsCount,
  .navCounter {
    color: #9ca3af;
  }
  
  .navButton {
    background: #374151;
    border-color: #4b5563;
    color: #d1d5db;
  }
  
  .navButton:hover:not(:disabled) {
    background-color: #4b5563;
    border-color: #6b7280;
  }
  
  .resultItem {
    border-bottom-color: #374151;
  }
  
  .resultItem:hover {
    background-color: #111827;
  }
  
  .resultItem.active {
    background-color: #1e3a8a;
    border-left-color: #60a5fa;
  }
  
  .resultText {
    color: #d1d5db;
  }
  
  .resultTime {
    color: #9ca3af;
  }
  
  .noResults {
    color: #9ca3af;
  }
}
