/* UserProfileModal.module.css */

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.modal {
  background: white;
  border-radius: 16px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow: hidden;
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #e5e7eb;
}

.title {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  color: #111827;
}

.closeButton {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  transition: all 0.2s;
}

.closeButton:hover {
  background-color: #f3f4f6;
  color: #374151;
}

.content {
  padding: 24px;
  overflow-y: auto;
  max-height: calc(90vh - 140px);
}

.avatarSection {
  text-align: center;
  margin-bottom: 32px;
}

.avatarContainer {
  position: relative;
  display: inline-block;
  margin-bottom: 16px;
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #f3f4f6;
}

.avatarPlaceholder {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  font-weight: 600;
  color: white;
  border: 4px solid #f3f4f6;
}

.avatarEditButton {
  position: absolute;
  bottom: 0;
  right: 0;
  background: #3b82f6;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  transition: all 0.2s;
  border: 3px solid white;
}

.avatarEditButton:hover {
  background: #2563eb;
  transform: scale(1.05);
}

.displayName {
  margin: 0 0 8px 0;
  font-size: 24px;
  font-weight: 600;
  color: #111827;
}

.username {
  margin: 0;
  font-size: 16px;
  color: #6b7280;
}

.profileInfo {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.infoSection {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.sectionTitle {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #111827;
  padding-bottom: 8px;
  border-bottom: 2px solid #f3f4f6;
}

.infoItem {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.infoIcon {
  color: #6b7280;
  margin-top: 4px;
  flex-shrink: 0;
}

.infoContent {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.infoLabel {
  font-size: 14px;
  font-weight: 500;
  color: #6b7280;
}

.infoValue {
  font-size: 16px;
  color: #111827;
  word-break: break-word;
}

.infoInput {
  width: 100%;
  padding: 8px 12px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.2s;
}

.infoInput:focus {
  border-color: #3b82f6;
}

.bioContainer {
  margin-top: 8px;
}

.bioText {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
  color: #111827;
  word-break: break-word;
}

.bioInput {
  width: 100%;
  padding: 12px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-size: 16px;
  font-family: inherit;
  outline: none;
  resize: vertical;
  min-height: 100px;
  transition: border-color 0.2s;
}

.bioInput:focus {
  border-color: #3b82f6;
}

.footer {
  padding: 20px 24px;
  border-top: 1px solid #e5e7eb;
  background-color: #f9fafb;
}

.editButton {
  width: 100%;
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: background-color 0.2s;
}

.editButton:hover {
  background: #2563eb;
}

.editButtons {
  display: flex;
  gap: 12px;
}

.cancelButton {
  flex: 1;
  background: white;
  color: #6b7280;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.cancelButton:hover {
  background-color: #f9fafb;
  border-color: #d1d5db;
}

.saveButton {
  flex: 1;
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.saveButton:hover {
  background: #2563eb;
}

/* Темная тема */
@media (prefers-color-scheme: dark) {
  .modal {
    background: #1f2937;
    color: white;
  }
  
  .header {
    border-bottom-color: #374151;
  }
  
  .title {
    color: #f9fafb;
  }
  
  .closeButton {
    color: #9ca3af;
  }
  
  .closeButton:hover {
    background-color: #374151;
    color: #d1d5db;
  }
  
  .avatar {
    border-color: #374151;
  }
  
  .avatarPlaceholder {
    border-color: #374151;
  }
  
  .displayName {
    color: #f9fafb;
  }
  
  .username {
    color: #9ca3af;
  }
  
  .sectionTitle {
    color: #f9fafb;
    border-bottom-color: #374151;
  }
  
  .infoIcon {
    color: #9ca3af;
  }
  
  .infoLabel {
    color: #9ca3af;
  }
  
  .infoValue {
    color: #f9fafb;
  }
  
  .infoInput {
    background: #374151;
    border-color: #4b5563;
    color: #f9fafb;
  }
  
  .infoInput:focus {
    border-color: #60a5fa;
  }
  
  .bioText {
    color: #f9fafb;
  }
  
  .bioInput {
    background: #374151;
    border-color: #4b5563;
    color: #f9fafb;
  }
  
  .bioInput:focus {
    border-color: #60a5fa;
  }
  
  .footer {
    border-top-color: #374151;
    background-color: #111827;
  }
  
  .cancelButton {
    background: #374151;
    color: #d1d5db;
    border-color: #4b5563;
  }
  
  .cancelButton:hover {
    background-color: #4b5563;
    border-color: #6b7280;
  }
}

/* Адаптивность */
@media (max-width: 640px) {
  .overlay {
    padding: 10px;
  }
  
  .modal {
    max-height: 95vh;
  }
  
  .content {
    padding: 20px;
    max-height: calc(95vh - 120px);
  }
  
  .header {
    padding: 16px 20px;
  }
  
  .footer {
    padding: 16px 20px;
  }
  
  .editButtons {
    flex-direction: column;
  }
}
